<template>
    <div id="news">
        新闻组件


        <ul>
            <li v-for="(item,key) in list">
                <router-link :to="'/content/'+item.key" v-html="item.value"></router-link>
            </li>
        </ul>
    </div>

</template>

<script>


export default {   
    data(){
        return{
            msg:'News -----compoment',
            list:[]
        }
    },methods:{
        requestData(){
            var api='http://baike.baidu.com/api/openapi/BaikeLemmaCardApi?scope=103&format=json&appid=379020&bk_key=银魂&bk_length=600'
            this.$http.jsonp(api).then((response)=>{
                //console.log(response);
                this.list=response.body.card;
            },(err)=>{
                console.log(err);
            })
        }
    },mounted(){
        this.requestData();
    }
    
}
</script>
    
