<template>
    <div id="content">
        <!-- <ul>
            <li v-for="item in list">
                {{ item }}
            </li>
        </ul> -->
        {{ msg }}
    </div>

</template>

<script>


export default {   
    data(){
        return{
            msg:'loading...',
            list:[]
        }
    },mounted(){
        //console.log(this.$route.params.key);//动态路由传值

        var id=this.$route.params.key;

        this.requestData(id);
    },methods:{
        requestData(id){
            var api='http://baike.baidu.com/api/openapi/BaikeLemmaCardApi?scope=103&format=json&appid=379020&bk_key=银魂&bk_length=600'
            this.$http.jsonp(api).then((response)=>{
                //console.log(response.body.card);
                for(var i=0;i<=13;i++){
                    if(response.body.card[i].key==id){
                        this.msg=response.body.card[i].name;
                    }
                }
                
            },(err)=>{
                console.log(err);
            })
        }
    }
    
}
</script>
    
